import React, { useState, useEffect } from "react";
import axios from 'axios';
import Success from "./Success";
import Notification from "./Notification";
// import {useNavigate } from 'react-router-dom'
const AddBill = (props) => {
  const [date, setDate] = useState("");
  const [amount, setAmount] = useState("");
  const [loading, setLoading] = useState(false); // New loading state
  const [error, setError] = useState(null); // New error state
  const [success , setSuccess] = useState(null);
  const handleDateChange = (e) => {
    setDate(e.target.value);
  };
  
  const handleAmountChange = (e) => {
    setAmount(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true); // Set loading to true while the request is in progress

    const bill = {
      date: date,
      amount: amount,
    };

    axios.post('/api/bills', { date, amount })
      .then((response) => {
        // console.log(response.data);
        setLoading(false); // Reset loading state
        setError(null); // Reset error state
        props.updateData([...props.initialData, bill]);
        setSuccess("Success!");
        setDate("");
        setAmount("");
        setTimeout(()=>{
          setSuccess(null)
        },5000);
      })
      .catch(error => {
        console.log(error);
        setLoading(false); // Reset loading state
        setError("An error occurred. Please try again."); // Set error message
      });
      setTimeout(()=>{
        setSuccess(null)
      },5000);
  };

  return (
    <div>
      <Success  message={success}/>
      <Notification message={error} />
      <form onSubmit={handleSubmit} className="addbill">
        <label htmlFor="date">Date</label>
        <input
          type="date"
          id="date"
          name="date"
          value={date}
          onChange={handleDateChange}
          required
        />
        <label htmlFor="amount">Amount</label>
        <input
          type="number"
          id="amount"
          name="amount"
          value={amount}
          onChange={handleAmountChange}
          required
        />
        <button type="submit" disabled={loading}>
          {loading ? "Adding..." : "Add Bill"} {/* Change button text based on loading state */}
        </button>
      </form>
      {error && <p>{error}</p>} {/* Display error message if there's an error */}
    </div>
  );
};

export default AddBill;
