import React, { useEffect, useState } from "react";
import AddBill from "./components/AddBill";
import Home from "./components/Home";
import axios from 'axios';
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";

import Success from "./components/Success";
import Notification from "./components/Notification";
function App() {
  const [totalamount, setTotalAmount] = useState(0);

  // Initialize the data state with your initial data
  const [data, setData] = useState([]);
  const calculateTotalAmount = (data) => {
    return data.reduce((acc, bill) => acc + parseFloat(bill.amount), 0);
  };
  
  useEffect(() => {
    axios.get('/api/bills/')
      .then((response) => {
        // console.log(response.data);
        setData(response.data);

        // Calculate the total amount from the response data
        const total = calculateTotalAmount(response.data);
        setTotalAmount(total);
      })
      .catch(error => {
        console.log(error);
      });
  }, []);

  // Function to update the data state
  const updateData = (newData) => {
    setData(newData);
  };

  return (
    <div>
      <div>
        <Router>
          <div className="routes">
            <Link to='/'>Home</Link>
            <Link to='/bills'>Bills</Link>
          </div>
          <h1>Total Amount: {totalamount}</h1>
          <Routes>
            <Route path="/bills" element={<Home initialData={data} updateData={updateData} />}/>
              <Route path="/" element={<AddBill initialData={data} updateData={updateData} />}/>

          </Routes>
        </Router>
      </div>
      

      {/* <AddBill initialData={data} updateData={updateData} /> */}
      

      {/* <Home initialData={data} updateData={updateData} /> */}
    </div>
  );
}

export default App;
