import React, { useState } from "react";
import Success from "./Success";
import Notification from "./Notification";
import axios from 'axios';
import {useNavigate } from 'react-router-dom'
const mongoose = require('mongoose');


const Home = (props) => {
  const [answer, setAnswer] = useState(""); // State to store user's answer
  const [error, setError] = useState(null); // New error state
  const [success , setSuccess] = useState(null);
  
  const formatDate = (dateString) => {
    const options = { year: 'numeric', month: 'long', day: 'numeric' };
    const formattedDate = new Date(dateString).toLocaleDateString(undefined, options);
    return formattedDate;
  };
  
  const handleDelete = (id) => {
    // Create a custom confirmation dialog with a question input
    const userAnswer = prompt("You already know it..?");
    // console.log("ID",id);
    const objectId = mongoose.Types.ObjectId(id);
    // console.log(objectId);
    // Check if the user's answer is correct
    if (userAnswer === "sriram82") {
      axios.delete(`/api/bills/${objectId}`)

        .then(response => {
          
          // console.log(id);
          // Update the UI by removing the deleted bill from the initialData array
          const updatedData = props.initialData.filter((bill) => bill._id !== id);
          setSuccess("Success!");
          props.updateData(updatedData);
          setTimeout(()=>{
            setSuccess(null)
          },5000);
        })
        .catch(error => {
          // console.log("error",id);
          setError("Error");
          setTimeout(()=>{
            setError(null)
          },5000);
          console.log(error);
        });
    } else {
      // User's answer is incorrect, do not delete the bill
      alert("Incorrect answer. Bill will not be deleted.");
    }
  };
  const sortedData = props.initialData.slice().sort((a, b) => b.amount - a.amount);

  return (  
    <div>
      <Success  message={success}/>
      <Notification message={error} />  
<div className="table-wrapper">
      

      <table>
        <thead>
          <tr>
            <th>Date</th>
            <th>Amount</th>
            <th>Action</th> {/* Add a column for the Delete button */}
          </tr>
        </thead>
        <tbody>
          {sortedData.map((val, key) => (
            <tr key={key}>
              <td>{formatDate(val.date)}</td>
              <td>{val.amount}</td>
              <td>
                <button className="delete-button" onClick={() => handleDelete(val._id)}>Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
    </div>
    
  );
};

export default Home;
